<template><section><h1>VcWallTrail</h1>
<p>The <code v-pre>vc-trail-wall</code> is used to load the flowing wall effect. Refer to <a href="https://www.wellyyss.cn/ysCesium/main/app.html">ysCesium|跃焱邵隼</a>.</p>
<h2>Example</h2>
<h3>Load a VcWallTrail</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-trail-wall :positions="positions" color="yellow" :minimumHeights="minimumHeights" imageUrl="./statics/SampleData/images/colors1.png" ref="wall" :interval="1000"></vc-trail-wall>
        <vc-layer-imagery>
          <vc-provider-imagery-tianditu mapStyle="img_c" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
