<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>positions</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code> Specifies an array of positions representing wall. <strong>Structure: [{ lng: number, lat: number, height: number },...,{ lng: number, lat: number, height: number }]</strong></td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> Specifies whether the wall is visible.</td>
</tr>
<tr>
<td>minimumHeights</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code> Specifies the height array of the bottom of the wall.</td>
</tr>
<tr>
<td>maximumHeights</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code> Specifies the height array of the top of the wall.</td>
</tr>
<tr>
<td>outline</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> Specifies whether the wall draws contour lines.</td>
</tr>
<tr>
<td>color</td>
<td>Object|String|Array</td>
<td><code v-pre>'yellow'</code></td>
<td><code v-pre>optional</code> Specify the color of the line.</td>
</tr>
<tr>
<td>interval</td>
<td>Number</td>
<td><code v-pre>3000</code></td>
<td><code v-pre>optional</code> Specify the time for the line material to flow for one cycle, in milliseconds.</td>
</tr>
<tr>
<td>imageUrl</td>
<td>String</td>
<td><code v-pre>''</code></td>
<td><code v-pre>optional</code> Specify the picture material of the line.</td>
</tr>
<tr>
<td>loop</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> Specifies whether the flow effect of the line is circular.</td>
</tr>
</tbody>
</table>
<hr>
<h2>Events</h2>
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject }</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    const lat = 30.598026044
    const lng = 114.302312702
    export default {
      data() {
        return {
          positions: [
            { lng: lng - 0.5, lat: lat - 0.5, height: 50000 },
            { lng: lng - 3, lat: lat, height: 0 },
            { lng: lng - 3, lat: lat - 1, height: 50000 },
            { lng: lng - 1, lat: lat - 3, height: 50000 },
            { lng: lng - 0.5, lat: lat - 0.5, height: 50000 }
          ],
          minimumHeights: [0, 0, 0, 0, 0]
        }
      },
      mounted() {
        this.$refs.wall.createPromise.then(({ Cesium, viewer, cesiumObject }) => {
          viewer.zoomTo(viewer.entities)
        })
      },
      methods: {
        ready(cesiumInstance) {
          this.cesiumInstance = cesiumInstance
          const { Cesium, viewer } = this.cesiumInstance
          viewer.scene.globe.depthTestAgainstTerrain = true
        }
      }
    }
  </script>